/*==================*/

/* Scrollbar */

/*==================*/

/* Width */

::-webkit-scrollbar {
  width: 7px;
}

/* Track */

::-webkit-scrollbar-track {
  background: #2F2F2F;
  /* background: #FFFFFF; */
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: #FFFFFF;
  /* background: #2F2F2F;  */
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: #42b6ff;
}

/*==================*/

/* Spacing */

/*==================*/

.navspace {
  padding-top: 100px;
}

/*==================*/

/* Font */

/*==================*/

.blue-text {
  color: #42b6ff !important;
}

.black-text {
  color: black !important;
}

.white-text {
  color: white !important;
}

.red-text {
  color: #bd4200 !important;
}

.yellow-text {
  color: #ffe342 !important;
}
.yellow-text, .red-text, .white-text, .black-text, .blue-text {
  font-family: 'Fjalla One', sans-serif;
}

.form {
  font-size: 30px;
}

a {
  color: white;
  ;
}

a:hover {
  color: #ffe342;
}

a:hover {
  text-decoration: none;
}

